import { ExceptionService } from './exception.service';
import { catchError } from 'rxjs/operators';
import { AuthService } from './auth.service';
import { Porcentaje } from './../_model/dto/porcentaje';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PorcentajeDepreciacionService {

  url: string = `${environment.HOST}/depreciaciones`;

  constructor(public http: HttpClient, public authService: AuthService, public exception: ExceptionService) { }

  getPorcentaje(idClasificacion: number, antiguedad: number, material: string, estado: string) {
    let intervalo: string;
    if (antiguedad <= 5) {
      intervalo = "<=5";
    }
    if (antiguedad > 5 && antiguedad <= 10) {
      intervalo = "<=10 , >5";
    }
    if (antiguedad > 10 && antiguedad <= 15) {
      intervalo = "<=15 , >10";
    }
    if (antiguedad > 15 && antiguedad <= 20) {
      intervalo = "<=20 , >15";
    }
    if (antiguedad > 20 && antiguedad <= 25) {
      intervalo = "<=25 , >20";
    }
    if (antiguedad > 25 && antiguedad <= 30) {
      intervalo = "<=30 , >25";
    }
    if (antiguedad > 30 && antiguedad <= 35) {
      intervalo = "<=35 , >30";
    }
    if (antiguedad > 35 && antiguedad <= 40) {
      intervalo = "<=40 , >35";
    }
    if (antiguedad > 40 && antiguedad <= 45) {
      intervalo = "<=45 , >40";
    }
    if (antiguedad > 45 && antiguedad <= 50) {
      intervalo = "<=50 , >45";
    }
    if (antiguedad > 50) {
      intervalo = ">50";
    }
    return this.http.get<Porcentaje>(`${this.url}/${idClasificacion}?intervalo=${intervalo}&material=${material}&estado=${estado}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
