import { TasaService } from './../../../_service/tasa.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Tasa } from './../../../_model/tasa';
import { Component, OnInit, Inject } from '@angular/core';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-procedimiento-dialog',
  templateUrl: './procedimiento-dialog.component.html',
  styleUrls: ['./procedimiento-dialog.component.scss']
})
export class ProcedimientoDialogComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<ProcedimientoDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public tasa: Tasa,
    public service: TasaService) { }

  ngOnInit() {
  }

  operar() {
    this.service.crear(this.tasa).subscribe(() => {
      Swal.fire({
        icon: 'success',
        title: 'Mensaje',
        text: 'Se registro el procedimiento con exito!'
      });
      this.service.listarPageable(0, 10).subscribe(data => {
        this.service.tasasSubject.next(data);
        this.dialogRef.close();
      });
    });
  }

  cancelar() {
    this.dialogRef.close();
  }

}
