import { ImpuestoDetailService } from './../../../_service/impuesto-detail.service';
import { ReportsService } from './../../../_controller/reports.service';
import { IngresoService } from './../../../_service/ingreso.service';
import { Tasa } from './../../../_model/tasa';
import { startWith, map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { FormControl } from '@angular/forms';
import { TasaService } from './../../../_service/tasa.service';
import { DetalleIngreso } from './../../../_model/detalle-ingreso';
import { ReciboService } from './../../../_service/recibo.service';
import { Recibo } from './../../../_model/dto/recibo';
import { MatDialogRef, MatTableDataSource, MAT_DIALOG_DATA } from '@angular/material';
import { Contribuyente } from './../../../_model/dto/contribuyente';
import { Empresa } from './../../../_model/empresa';
import { Persona } from './../../../_model/persona';
import { EmpresaService } from './../../../_service/empresa.service';
import { PersonaService } from './../../../_service/persona.service';
import { Component, OnInit, Inject } from '@angular/core';
import Swal from 'sweetalert2';
import * as moment from 'moment';
import { SelectionModel } from '@angular/cdk/collections';

@Component({
  selector: 'app-pago-dialog',
  templateUrl: './pago-dialog.component.html',
  styleUrls: ['./pago-dialog.component.scss']
})
export class PagoDialogComponent implements OnInit {

  dataSource: MatTableDataSource<any>;
  displayedColumns = ['select', 'periodo', 'anual', 'trimestral', 'diferencia', 'pagado', 'estado'];
  enabled = true;
  tipoSelected: any;
  procedimientosCtrl: FormControl[] = [];
  preciosCtrl: FormControl[] = [];
  procedimientosFiltrados: Observable<Tasa[]>[] = [];
  total: number = 0;
  tasas: Tasa[];
  tasasCopy: Tasa[];
  selection = new SelectionModel<any>(true, []);

  constructor(public dialogRef: MatDialogRef<PagoDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public recibo: Recibo,
    public reciboService: ReciboService,
    public personaService: PersonaService,
    public empresaService: EmpresaService,
    public tasaService: TasaService,
    public ingresoService: IngresoService,
    public reportService: ReportsService,
    public impuestoDetailService: ImpuestoDetailService) { }

  ngOnInit() {
    this.impuestoDetailService.impuestosSubject.subscribe(data => {
      this.dataSource = new MatTableDataSource(data);
    });
    if (this.recibo.ingreso.id) {
      if (this.recibo.propietarioDetail.persona) {
        let persona = this.recibo.propietarioDetail.persona;
        this.tipoSelected = this.tipoDocumentos.filter(data => data.documento == persona.tipoDocumento)[0];
        this.contribuyente.id = persona.id;
        this.contribuyente.tipoDocumento = persona.tipoDocumento;
        this.contribuyente.documento = persona.documento;
        this.contribuyente.nombre = persona.nombres;
        this.impuestoDetailService.listarPorPropietarioId(persona.id).subscribe(impuestos => this.impuestoDetailService.impuestosSubject.next(impuestos))
      } else if (this.recibo.propietarioDetail.empresa) {
        let empresa = this.recibo.propietarioDetail.empresa;
        this.tipoSelected = this.tipoDocumentos.filter(data => data.documento == "RUC")[0];
        this.contribuyente.id = empresa.id;
        this.contribuyente.nombre = empresa.razonSocial;
        this.contribuyente.documento = empresa.ruc;
        this.contribuyente.representante = empresa.representante;
        this.impuestoDetailService.listarPorPropietarioId(empresa.id).subscribe(impuestos => this.impuestoDetailService.impuestosSubject.next(impuestos))
      }
      this.procedimientosCtrl = this.recibo.ingreso.detalle.map(detalle => new FormControl());
    }
    this.tasaService.listar().subscribe(data => {
      this.tasas = data;
      this.tasasCopy = data;
      if (this.recibo.ingreso.id) {
        this.recibo.ingreso.detalle.forEach((data, index) => {
          this.procedimientosFiltrados.push(this.procedimientosCtrl[index].valueChanges
            .pipe(
              startWith(''),
              map(procedimiento => {
                if (procedimiento) {
                  return this._filterProcedimientos(procedimiento);
                } else {
                  return this.tasasCopy.slice()
                }
              })
            ))
        })
      }
    });
  }

  personas: Persona[] = [];
  empresas: Empresa[] = [];
  tipoSelectedRepresentante: any;
  // ejemplos
  contribuyente = new Contribuyente();
  tipoDocumentos: any[] = [
    { documento: "DNI", length: 8, ejemplo: "75896541", meta: { nombre: "Apellidos y Nombres", placeholder: "RODRIGUEZ ARTEAGA FRANCISCO ROBERTO" } },
    { documento: "RUC", length: 11, ejemplo: "20542596352", meta: { nombre: "Razon Social", placeholder: "GRUPO ARCOS SRL" } },
    { documento: "CE", length: 20, ejemplo: "001043328", meta: { nombre: "Nombres", placeholder: "GIANLUCA LAPADULA" } }
  ];
  tipoDocumentosRepresentante: any[] = [
    { documento: "DNI", length: 8, ejemplo: "75896541", meta: { nombre: "Apellidos y Nombres", placeholder: "RODRIGUEZ ARTEAGA FRANCISCO ROBERTO" } },
    { documento: "CE", length: 20, ejemplo: "001043328", meta: { nombre: "Nombres", placeholder: "GIANLUCA LAPADULA" } }
  ];

  findConstribuyente(tipo: string, documento: string) {
    if (tipo == "RUC") {
      this.empresaService.findByRuc(documento).subscribe(empresa => {
        if (empresa.id != null) {
          this.impuestoDetailService.listarPorPropietarioId(empresa.id).subscribe(impuestos => this.impuestoDetailService.impuestosSubject.next(impuestos));
          this.contribuyente.id = empresa.id;
          this.contribuyente.nombre = empresa.razonSocial;
          this.contribuyente.documento = empresa.ruc;
          this.contribuyente.representante = empresa.representante;
          if (empresa.representante.tipoDocumento === "DNI") {
            this.tipoSelectedRepresentante = { documento: "DNI", length: 8, ejemplo: "75896541", meta: { nombre: "Apellidos y Nombres", placeholder: "RODRIGUEZ ARTEAGA FRANCISCO ROBERTO" } };
          } else if (empresa.representante.tipoDocumento === "CE") {
            this.tipoSelectedRepresentante = { documento: "CE", length: 20, ejemplo: "001043328", meta: { nombre: "Nombres", placeholder: "GIANLUCA LAPADULA" } };
          }
        }
      });
    } else {
      this.personaService.findByTipoAndDocumento(tipo, documento).subscribe(persona => {
        if (persona.id != null) {
          this.impuestoDetailService.listarPorPropietarioId(persona.id).subscribe(impuestos => this.impuestoDetailService.impuestosSubject.next(impuestos))
          this.contribuyente.id = persona.id;
          this.contribuyente.tipoDocumento = persona.tipoDocumento;
          this.contribuyente.documento = persona.documento;
          this.contribuyente.nombre = persona.nombres;
        }
      });
    }
    this.recibo.ingreso.detalle.forEach(detalle => {
      if (detalle.impuesto) {
        this.remove(detalle);
      }
    });
  }

  findRepresentante(tipo: string, documento: string) {
    this.personaService.findByTipoAndDocumento(tipo, documento).subscribe(persona => {
      if (persona.id != null) {
        this.contribuyente.representante.id = persona.id;
        this.contribuyente.representante.tipoDocumento = persona.tipoDocumento;
        this.contribuyente.representante.documento = persona.documento;
        this.contribuyente.representante.nombres = persona.nombres;
      }
    });
  }

  compare(option1: any, option2: any) {
    return option1.documento === option2.documento;
  }

  agregar() {
    let detalle = new DetalleIngreso();
    this.recibo.ingreso.detalle.push(detalle);
    this.procedimientosCtrl.push(new FormControl());
    this.preciosCtrl.push(new FormControl());
    this.procedimientosFiltrados.push(this.procedimientosCtrl[this.recibo.ingreso.detalle.length - 1].valueChanges
      .pipe(
        startWith(''),
        map(procedimiento => {
          if (procedimiento) {
            return this._filterProcedimientos(procedimiento);
          } else {
            return this.tasasCopy.slice()
          }
        })
      ));
    this.preciosCtrl[this.recibo.ingreso.detalle.length - 1].valueChanges.subscribe(precio => {
      if (precio) {
        this.total = this.recibo.ingreso.detalle.map(ele => ele.precio).reduce((sum, ele) => sum + ele);
      }
    });
  }

  remove(detalle: DetalleIngreso) {
    const index = this.recibo.ingreso.detalle.indexOf(detalle);
    if (index >= 0) {
      this.recibo.ingreso.detalle.splice(index, 1);
    }
  }

  public _filterProcedimientos(value: string): Tasa[] {
    const filterValue = value.toLowerCase();
    return this.tasasCopy.filter(tasa => tasa.procedimiento.toLowerCase().indexOf(filterValue) === 0);
  }

  validarPrecio(detalle: DetalleIngreso) {
    detalle.tasa = this.tasas.filter(tasa => tasa.procedimiento.toLowerCase() == detalle.concepto.toLowerCase())[0];
    if (detalle.tasa) {
      detalle.precio = detalle.tasa.precio;
    }
  }

  save(tipoDocumento: any, tipoDocumentoRepresentante: any) {
    this.contribuyente.tipoDocumento = tipoDocumento.documento;
    if (this.contribuyente.tipoDocumento == "DNI" || this.contribuyente.tipoDocumento == "CE") {
      let persona = new Persona();
      persona.id = this.contribuyente.id;
      persona.tipoDocumento = this.contribuyente.tipoDocumento;
      persona.documento = this.contribuyente.documento;
      persona.nombres = this.contribuyente.nombre;
      this.recibo.propietarioDetail.persona = persona;
    } else if (this.contribuyente.tipoDocumento == "RUC") {
      let empresa = new Empresa();
      empresa.id = this.contribuyente.id;
      empresa.razonSocial = this.contribuyente.nombre;
      empresa.ruc = this.contribuyente.documento;
      empresa.representante = this.contribuyente.representante;
      empresa.representante.tipoDocumento = tipoDocumentoRepresentante.documento;
      this.recibo.propietarioDetail.empresa = empresa;
    }
    this.reciboService.crear(this.recibo).subscribe(data => {
      Swal.fire({
        title: 'Se registro el pago',
        showDenyButton: true,
        confirmButtonText: `Imprimir comprobante`,
        denyButtonText: `Cancelar`,
        icon: "success"
      }).then((result) => {
        if (result.isConfirmed) {
          this.reportService.openRecibo(data);
        }
      })
      this.ingresoService.listarPageable(0, 10).subscribe(data => {
        data.content.forEach(element => {
          element.createdAt = moment(element.createdAt).format("DD/MM/YYYY HH:mm:ss");
        });
        this.ingresoService.ingresosSubject.next(data);
      });
      this.dialogRef.close();
    });
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    let numRows = 0;
    if (this.dataSource) {
      numRows = this.dataSource.data.length;
    }
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    if (this.isAllSelected()) {
      this.selection.clear();
      this.recibo.ingreso.detalle = [];
      return;
    } else {
      this.recibo.ingreso.detalle = this.recibo.ingreso.detalle.filter(detalle => !detalle.impuesto);
      this.dataSource.data.forEach(elemento => this.selected({ checked: true }, elemento));
    }
    this.selection.select(...this.dataSource.data);
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? 'deselect' : 'select'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.position + 1}`;
  }

  selected(e: any, row: any) {
    if (e.checked) {
      let detalle = new DetalleIngreso();
      detalle.impuesto = row.impuesto;
      detalle.precio = row.impuesto.diferencia;
      detalle.concepto = `Impuesto Predial ao ${row.periodo.anio}`;
      this.recibo.ingreso.detalle.push(detalle);
      this.procedimientosCtrl.push(new FormControl());
      this.preciosCtrl.push(new FormControl());
      this.procedimientosFiltrados.push(this.procedimientosCtrl[this.recibo.ingreso.detalle.length - 1].valueChanges
        .pipe(
          startWith(''),
          map(procedimiento => {
            if (procedimiento) {
              return this._filterProcedimientos(procedimiento);
            } else {
              return this.tasasCopy.slice()
            }
          })
        ));
      this.preciosCtrl[this.recibo.ingreso.detalle.length - 1].valueChanges.subscribe(precio => {
        if (precio) {
          this.total = this.recibo.ingreso.detalle.map(ele => ele.precio).reduce((sum, ele) => sum + ele);
        }
      });
    } else {
      let detalle = this.recibo.ingreso.detalle.filter(detalle => detalle.impuesto.id == row.impuesto.id)[0];
      const index = this.recibo.ingreso.detalle.indexOf(detalle);
      if (index >= 0) {
        this.recibo.ingreso.detalle.splice(index, 1);
      }
    }
    console.log(row.impuesto);
    e ? this.selection.toggle(row) : null
  }
}
