import { UsuarioService } from './../../../_service/usuario.service';
import { Usuario } from './../../../_model/usuario';
import { AuthService } from './../../../_service/auth.service';
import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-perfil',
  templateUrl: './perfil.component.html',
  styleUrls: ['./perfil.component.scss']
})
export class PerfilComponent implements OnInit {

  public user: Usuario;
  enabledPassword: boolean = false;
  newPass0 = "";
  newPass1 = "";
  constructor(
    public service: AuthService,
    public usuarioService: UsuarioService
  ) { }

  ngOnInit() {
    this.usuarioService
      .listarPorUsername(this.service.usuario.nombre)
      .subscribe((data) => {
        this.user = data;
        this.user.contrasenia = "";
      });
  }

  grabar() {
    this.service.login(this.user).subscribe(
      (res) => {
        this.user.contrasenia = this.newPass0;
      },
      (error) => {
        if (error.status == 400) {
          Swal.fire("Error", "Clave incorrecta!", "error");
        }
      },
      () => {
        if (this.newPass0 == this.newPass1) {
          this.usuarioService
            .modificar(this.user)
            .subscribe(() =>
              Swal.fire("xito", "Se cambio la contrasea con xito", "success")
            );
        } else {
          Swal.fire("Error", "Las contraseas no coinciden", "error");
        }
      }
    );
  }

}
