import { AuthService } from './auth.service';
import { ExceptionService } from './exception.service';
import { Periodo } from './../_model/periodo';
import { environment } from './../../environments/environment';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})
export class PeriodoService {

  periodosSubject = new Subject<any>();

  url: string = `${environment.HOST}/periodos`;

  constructor(public http: HttpClient, public exception: ExceptionService, public authService: AuthService) { }

  listar() {
    return this.http.get<Periodo[]>(this.url, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  listarPageable(p: number, s: number) {
    return this.http.get<any>(`${this.url}/pageable?page=${p}&size=${s}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  listarPorId(id: number) {
    return this.http.get<Periodo>(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  crear(periodo: Periodo) {
    return this.http.post(this.url, periodo, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  modificar(periodo: Periodo) {
    return this.http.put(this.url, periodo, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  eliminar(id: number) {
    return this.http.delete(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
