import { AuthService } from './auth.service';
import { Ingreso } from './../_model/ingreso';
import { catchError } from 'rxjs/operators';
import { throwError, Subject } from 'rxjs';
import { environment } from './../../environments/environment';
import { ExceptionService } from './exception.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class IngresoService {

  url: string = `${environment.HOST}/ingresos`;
  ingresosSubject = new Subject<any>();

  constructor(public http: HttpClient, public exception: ExceptionService, public authService: AuthService) { }

  listarPorFechas(from: string, to: string) {
    return this.http.get<Ingreso[]>(`${this.url}?from=${from}&to=${to}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  listarPageable(page: number, size: number, from?: string, to?: string, propietarioName?: string) {
    let route = "";
    if (from && to) {
      route += `&from=${from}&to=${to}`;
    }
    if (propietarioName) {
      route += `&propietarioName=${propietarioName}`;
    }
    return this.http.get<any>(`${this.url}/pageable?page=${page}&size=${size}${route}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
