import { catchError } from 'rxjs/operators';
import { ExceptionService } from './exception.service';
import { AuthService } from './auth.service';
import { Persona } from './../_model/persona';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PersonaService {

  url: string = `${environment.HOST}/personas`;

  constructor(public http: HttpClient, public authService: AuthService, public exception: ExceptionService) { }

  consultarReniec(dni: string) {
    return this.http.get<Persona>(`/dni/${dni}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  findByTipoAndDocumento(tipo: string, documento: string) {
    return this.http.get<Persona>(`${this.url}/${tipo}/${documento}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
