import { ValorUnitEdificacion } from './../../_model/valor-unit-edificacion';
import { ValorUnitEdificacionService } from './../../_service/valor-unit-edificacion.service';
import { UnitEdificacionService } from './../../_service/unit-edificacion.service';
import { CookiesService } from './../../_controller/cookies.service';
import { PeriodoDialogoComponent } from './../periodo/periodo-dialogo/periodo-dialogo.component';
import { Periodo } from './../../_model/periodo';
import { PeriodoService } from './../../_service/periodo.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort, MatDialog, MatBottomSheet } from '@angular/material';
import { BottomSheetMensajeComponent } from './bottom-sheet-mensaje/bottom-sheet-mensaje.component';
import { UnitEdificacion } from 'src/app/_model/unit-edificacion';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-mantenimiento',
  templateUrl: './mantenimiento.component.html',
  styleUrls: ['./mantenimiento.component.scss']
})
export class MantenimientoComponent implements OnInit {

  periodos: Periodo[]
  valoresUnitarios: ValorUnitEdificacion[];
  unitariosEdificacion: UnitEdificacion[];
  displayedColumns: string[] = ['id', 'anio', 'uit', 'acciones'];
  dataSource: MatTableDataSource<Periodo>;
  seleccionado: any;
  isActive: boolean;
  isActive1: boolean;
  isActive2: boolean;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  constructor(
    public unitEdificacionService: UnitEdificacionService,
    public dialog: MatDialog, public mensaje: MatBottomSheet,
    public valorUnitService: ValorUnitEdificacionService,
    public periodoService: PeriodoService,
    public cookieService: CookiesService) { }

  ngOnInit() {

    this.periodoService.listar().subscribe(data => {
      this.periodoService.periodosSubject.next(data);
    });

    this.periodoService.periodosSubject.subscribe(data => {
      this.dataSource = new MatTableDataSource(data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.openMensaje();
      this.periodos = data;
    });

    this.unitEdificacionService.listar().subscribe(data => {
      this.unitariosEdificacion = data;
    });
  }

  openDialog(periodo?: Periodo) {
    let periodo1 = periodo != null ? periodo : new Periodo();
    this.dialog.open(PeriodoDialogoComponent, {
      width: '450px',
      data: periodo1
    });
  }

  openMensaje() {
    let cook = this.cookieService.getCookie('mensajeMantenimiento');
    if (cook == 'false') {
      this.mensaje.open(BottomSheetMensajeComponent);
    }
  }

  cargarValores(periodoId: number) {
    this.valorUnitService.listar(periodoId).subscribe(data => {
      if (data.length == 0) {
        console.log("no hay datos");
        let valorUnitario: ValorUnitEdificacion;
        this.valoresUnitarios = [];
        for (let i = 0; i < 59; i++) {
          valorUnitario = new ValorUnitEdificacion();
          valorUnitario.periodo = new Periodo();
          valorUnitario.periodo.id = periodoId;
          valorUnitario.unitEdificacion = this.unitariosEdificacion[i];
          valorUnitario.valor = 0.00;
          this.valoresUnitarios.push(valorUnitario);
        }
      } else {
        this.valoresUnitarios = data;
      }
    });
  }

  grabar() {
    this.valorUnitService.grabar(this.valoresUnitarios).subscribe(data => {
      Swal.fire("Muy bien", "Se registraron los valores con exito!", "success");
    });
  }

  eliminar(periodo: Periodo) {
    Swal.fire({
      title: `Esta seguro que desea eliminar el periodo ${periodo.anio}?`,
      showDenyButton: true,
      confirmButtonText: `Aceptar`,
      denyButtonText: `Cancelar`,
      icon: "warning"
    }).then((result) => {
      if (result.isConfirmed) {
        this.periodoService.eliminar(periodo.id).subscribe(() => Swal.fire("Se elimino con exito", "", "success"));
      }
    })
  }
}