import { AuthService } from './auth.service';
import { AutoavaluoCpu } from './../_model/dto/autoavaluo-cpu';
import { ExceptionService } from './exception.service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AutoavaluoCpuService {

  url: string = `${environment.HOST}/autoavaluo/cpu`;

  constructor(public http: HttpClient, public exception: ExceptionService, public authService: AuthService) { }

  crear(autoavaluoCpu: AutoavaluoCpu) {
    return this.http.post<AutoavaluoCpu>(this.url, autoavaluoCpu, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  getByPredioId(id: number) {
    return this.http.get<AutoavaluoCpu>(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  deleteByPredioId(id: number) {
    return this.http.delete(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
