import { InstFijaPerman } from './../../_model/inst-fija-perman';
import { InstalacionPredio } from './../../_model/instalacion-predio';
import { Periodo } from './../../_model/periodo';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { InstalacionService } from 'src/app/_service/instalacion.service';

@Component({
  selector: 'app-instalacion',
  templateUrl: './instalacion.component.html',
  styleUrls: ['./instalacion.component.scss']
})
export class InstalacionComponent implements OnInit {

  @Output()
  instalacionesChange = new EventEmitter();

  @Input()
  instalacionesPredio: InstalacionPredio[];

  instalaciones: InstFijaPerman[] = [];
  generales: string[][] = [];
  especificas: InstFijaPerman[][] = [[]];
  todasGenerales: string[];

  constructor(public instalacionService: InstalacionService) { }

  ngOnInit() {
    if (!this.instalacionesPredio) {
      this.instalacionesPredio = [];
    }
    // if (this.periodo) {
    this.instalacionService.listar().subscribe(instalaciones => {
      this.instalaciones = instalaciones;
      this.todasGenerales = instalaciones.map(instalacion => instalacion.descripcionGeneral);
    });

    // }
  }

  agregar() {
    let instalacionPredio = new InstalacionPredio();
    instalacionPredio.instalacion = new InstFijaPerman();
    instalacionPredio.unidades = 0;
    this.instalacionesPredio.push(instalacionPredio);
    this.instalacionesChange.emit(this.instalacionesPredio);
    this.generales.push(this.todasGenerales.filter((general, index) => this.todasGenerales.indexOf(general) == index));
  }

  remove(instalacionPredio: InstalacionPredio) {
    const index = this.instalacionesPredio.indexOf(instalacionPredio);
    if (index >= 0) {
      this.instalacionesPredio.splice(index, 1);
      this.generales.splice(index, 1);
      this.especificas.splice(index, 1);
    }
  }

  filtrarEspecifico(index: number, general: any) {
    this.instalacionesPredio[index].instalacion = undefined;
    this.especificas[index] = this.instalaciones.filter(instalacion => instalacion.descripcionGeneral == general.value);
    // this.especificas = this.instalaciones.filter(instalacion => instalacion.descripcionGeneral == general);
  }

}
