import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class GeocodeService {

  url: string = `https://maps.googleapis.com/maps/api/geocode/json?key=${environment.API_KEY}`;
  constructor(public http: HttpClient) { }

  getCoordenadas(address: string) {
    return this.http.get<any>(`${this.url}&address=${address}`);
  }

  getAddress(lat: number, lng: number) {
    return this.http.get<any>(`${this.url}&latlng=${lat},${lng}`);
  }
}
