import { ReportsService } from './../../../_controller/reports.service';
import { IngresoService } from './../../../_service/ingreso.service';
import { MatDialogRef } from '@angular/material';
import { Component, OnInit } from '@angular/core';
import * as moment from 'moment';

@Component({
  selector: 'app-caja',
  templateUrl: './caja.component.html',
  styleUrls: ['./caja.component.scss']
})
export class CajaComponent implements OnInit {

  desde = new Date();
  hasta = new Date();
  constructor(public dialogRef: MatDialogRef<CajaComponent>,
    public ingresoService: IngresoService,
    public reportService: ReportsService) { }

  ngOnInit() {

  }

  generarReporte() {
    let from = `${moment(this.desde).format("YYYY-MM-DDT")}00:00:00.000`;
    let to = `${moment(this.hasta).format("YYYY-MM-DDT")}23:59:59.000`;
    this.ingresoService.listarPorFechas(from, to).subscribe(ingresos => this.reportService.openReporteCaja(from, to, ingresos));
  }
}
