import { Router } from '@angular/router';
import { AuthService } from './../../../_service/auth.service';
import { Usuario } from './../../../_model/usuario';
import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  hide: boolean;
  usuario: Usuario;

  constructor(public service: AuthService, public router: Router) {
    this.usuario = new Usuario();
  }

  ngOnInit() {
    if (this.service.isAuthenticated()) {
      this.router.navigate(['/']);
    }
  }

  login() {
    if (this.usuario.nombre == null || this.usuario.contrasenia == null) {
      Swal.fire('Error Login', 'Usuario o Password vacias!', 'error');
      return;
    }

    this.service.login(this.usuario).subscribe(res => {
      this.service.guardarUsuario(res.access_token);
      this.service.guardarToken(res.access_token);
      window.location.reload();
    }, error => {
      if (error.status == 400) {
        Swal.fire('Error Login', 'Usuario o Clave incorrectas!', 'error');
      }
    });
  }

}
