import { Periodo } from './../../_model/periodo';
import { PeriodoService } from './../../_service/periodo.service';
import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'app-periodo',
  templateUrl: './periodo.component.html',
  styleUrls: ['./periodo.component.scss']
})
export class PeriodoComponent implements OnInit {

  @Input()
  periodo: Periodo;
  @Output()
  emisor = new EventEmitter();

  seleccionado: Periodo;

  periodos: Periodo[];
  constructor(public service: PeriodoService) { }

  ngOnInit() {
    this.service.listar().subscribe(data => {
      this.periodos = data;
      if (this.periodo) {
        this.seleccionado = data.filter(element => element.id == this.periodo.id)[0];
      }
    });

    this.service.periodosSubject.subscribe(data => {
      console.log("suscripcion");
      console.log(data);
      this.periodos = data;
    });
  }

}
