import { AuthService } from './auth.service';
import { catchError } from 'rxjs/operators';
import { AutoavaluoRustico } from './../_model/dto/autoavaluo-rustico';
import { ExceptionService } from './exception.service';
import { environment } from './../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AutoavaluoRusticoService {

  url: string = `${environment.HOST}/autoavaluo/rustico`;

  constructor(public http: HttpClient, public exception: ExceptionService, public authService: AuthService) { }

  crear(autoavaluoRustico: AutoavaluoRustico) {
    return this.http.post<AutoavaluoRustico>(this.url, autoavaluoRustico, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  getByPredioId(id: number) {
    return this.http.get<AutoavaluoRustico>(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  deleteByPredioId(id: number) {
    return this.http.delete(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
