import { AuthService } from './auth.service';
import { ExceptionService } from './exception.service';
import { catchError } from 'rxjs/operators';
import { AutoavaluoUrbano } from './../_model/dto/autoavaluo-urbano';
import { environment } from './../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AutoavaluoUrbanoService {

  url: string = `${environment.HOST}/autoavaluo/urbano`;

  constructor(public http: HttpClient, public exception: ExceptionService, public authService: AuthService) { }

  crear(autoavaluoUrbano: AutoavaluoUrbano) {
    return this.http.post<AutoavaluoUrbano>(this.url, autoavaluoUrbano, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  getByPredioId(id: number) {
    return this.http.get<AutoavaluoUrbano>(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  deleteByPredioId(id: number) {
    return this.http.delete(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
