import { UsuarioComponent } from './pages/usuario/usuario/usuario.component';
import { PerfilComponent } from './pages/usuario/perfil/perfil.component';
import { LoginComponent } from './pages/usuario/login/login.component';
import { PagoComponent } from './pages/pago/pago.component';
import { ProcedimientoComponent } from './pages/procedimiento/procedimiento.component';
import { UrbanoComponent } from './pages/mantenimiento/urbano/urbano.component';
import { ReportesComponent } from './pages/reportes/reportes.component';
import { PredioComponent } from './pages/predio/predio.component';
import { RusticoComponent } from './pages/mantenimiento/rustico/rustico.component';
import { CpuComponent } from './pages/mantenimiento/cpu/cpu.component';
import { MantenimientoComponent } from './pages/mantenimiento/mantenimiento.component';
import { HomeComponent } from './pages/home/home.component';
import { PeriodoComponent } from './pages/periodo/periodo.component';
import { AppComponent } from './app.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  { path: 'periodo', component: PeriodoComponent },
  { path: '', component: HomeComponent },
  { path: 'mantenimiento', component: MantenimientoComponent },
  { path: 'mantenimiento/cpu', component: CpuComponent },
  { path: 'mantenimiento/rustico', component: RusticoComponent },
  { path: 'mantenimiento/urbano', component: UrbanoComponent },
  { path: 'predio', component: PredioComponent },
  { path: 'procedimiento', component: ProcedimientoComponent },
  { path: 'pago', component: PagoComponent },
  // { path: 'predio/urbano', component: PredioUrbanoComponent },
  // { path: 'predio/cpu', component: PredioCpuComponent },
  // { path: 'predio/rustico', component: PredioRusticoComponent },
  { path: 'reportes', component: ReportesComponent },
  { path: 'login', component: LoginComponent },
  { path: 'perfil', component: PerfilComponent },
  { path: 'usuarios', component: UsuarioComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
