import { UsuarioDialogComponent } from './../usuario-dialog/usuario-dialog.component';
import { UsuarioService } from './../../../_service/usuario.service';
import { Usuario } from './../../../_model/usuario';
import { MatTableDataSource, MatSort, MatDialog } from '@angular/material';
import { Component, OnInit, ViewChild } from '@angular/core';
import * as moment from 'moment';
import { Persona } from 'src/app/_model/persona';

@Component({
  selector: 'app-usuario',
  templateUrl: './usuario.component.html',
  styleUrls: ['./usuario.component.scss']
})
export class UsuarioComponent implements OnInit {

  dataSource: MatTableDataSource<Usuario>;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  displayedColumns = ['creacion', 'modificacion', 'nombre', 'usuario', 'acciones'];

  constructor(public usuarioService: UsuarioService,
    public dialog: MatDialog) { }

  ngOnInit() {
    this.usuarioService.listar().subscribe(data => {
      data.forEach(element => {
        element.fechaCreacion = moment(element.fechaCreacion).format("DD/MM/YYYY HH:mm:ss");
        element.fechaModificacion = moment(element.fechaModificacion).format("DD/MM/YYYY HH:mm:ss");
      });
      this.usuarioService.usuarioSubject.next(data);
    });
    this.usuarioService.usuarioSubject.subscribe(data => {
      data.forEach(element => {
        element.fechaCreacion = moment(element.fechaCreacion).format("DD/MM/YYYY HH:mm:ss");
        element.fechaModificacion = moment(element.fechaModificacion).format("DD/MM/YYYY HH:mm:ss");
      });
      this.dataSource = new MatTableDataSource(data);
      this.dataSource.sort = this.sort;
    });
  }

  openDialog(usuario?: Usuario) {
    if (!usuario) {
      usuario = new Usuario();
      usuario.persona = new Persona();
      usuario.roles = [];
    }
    this.dialog.open(UsuarioDialogComponent, {
      width: '600px',
      data: usuario
    });
  }

}
