import { ExceptionService } from './exception.service';
import { catchError } from 'rxjs/operators';
import { AuthService } from './auth.service';
import { ValorTerrenoCP } from './../_model/valorTerrenoCP';
import { HttpClient } from '@angular/common/http';
import { environment } from './../../environments/environment';
import { Injectable } from '@angular/core';
import { throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ValorTerrenoCpService {

  url: string = `${environment.HOST}/valorterrenocp`;

  constructor(public http: HttpClient, public authService: AuthService, public exception: ExceptionService) { }

  listar(anio: number) {
    return this.http.get<ValorTerrenoCP[]>(`${this.url}/periodo/${anio}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  grabar(valoresTerrenoCP: ValorTerrenoCP[]) {
    return this.http.post(this.url, valoresTerrenoCP, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
