import { IngresoService } from './../../_service/ingreso.service';
import { CajaComponent } from './caja/caja.component';
import { MatDialog } from '@angular/material';
import { Component, OnInit } from '@angular/core';
import { PdfMakeWrapper } from 'pdfmake-wrapper';
import pdfFonts from "pdfmake/build/vfs_fonts";
import { Chart, registerables } from 'chart.js';
import * as moment from 'moment';

@Component({
  selector: 'app-reportes',
  templateUrl: './reportes.component.html',
  styleUrls: ['./reportes.component.scss']
})
export class ReportesComponent implements OnInit {

  chartIngreso: any;
  chartIngresoBar: any;

  constructor(public dialog: MatDialog,
    public ingresoService: IngresoService) {
    Chart.register(...registerables);
  }

  ngOnInit() {
    let actual = new Date();
    let desde = moment(`${actual.getFullYear()}/${actual.getMonth() + 1}/01`).format("YYYY-MM-DDTHH:mm:ss.SSS");
    let hasta = moment(desde).add(1, 'month').add(-1, 'millisecond').format("YYYY-MM-DDTHH:mm:ss.SSS");
    let days = [];
    let ingresos = [];
    for (let i = 1; i <= parseInt(hasta.substring(8, 10)); i++) {
      days.push(i);
    }
    this.ingresoService.listarPorFechas(desde, hasta).subscribe(data => {
      days.forEach(dia => {
        ingresos.push(data.filter(ingreso => dia == parseInt(ingreso.createdAt.substring(8, 10))).map(ingreso => ingreso.montoAfecto).reduce((sum, element) => sum + element, 0));
      });
      this.chartIngreso = new Chart('ingresoChart', {
        type: 'line',
        data: {
          labels: days,
          datasets: [
            {
              label: `Mes de ${moment(desde).lang("es").format("MMMM")}`,
              data: ingresos,
              borderColor: "#5bc0de",
              borderWidth: 3,
              fill: false
            }
          ]
        }
      });
      this.chartIngresoBar = new Chart('ingresoChartBar', {
        type: 'bar',
        data: {
          labels: days,
          datasets: [
            {
              label: `Mes de ${moment(desde).lang("es").format("MMMM")}`,
              data: ingresos,
              borderColor: "#5bc0de",
              borderWidth: 3
            }
          ]
        }
      });
    });
    PdfMakeWrapper.setFonts(pdfFonts);
  }

  openReportCaja() {
    this.dialog.open(CajaComponent, {
      width: '400px'
    })
  }
}
