import { PeriodoService } from './../../../_service/periodo.service';
import { Periodo } from './../../../_model/periodo';
import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-periodo-dialogo',
  templateUrl: './periodo-dialogo.component.html',
  styleUrls: ['./periodo-dialogo.component.scss']
})
export class PeriodoDialogoComponent implements OnInit {

  periodo: Periodo;

  constructor(public dialogRef: MatDialogRef<PeriodoDialogoComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Periodo,
    public service: PeriodoService) { }

  ngOnInit() {
    this.periodo = new Periodo();
    if (this.data.id > 0) {
      this.periodo.id = this.data.id;
      this.periodo.anio = this.data.anio;
      this.periodo.uit = this.data.uit;
    }
  }

  operar() {
    this.service.crear(this.periodo).subscribe(() => {
      Swal.fire({
        icon: 'success',
        title: 'Mensaje',
        text: 'Se registro el periodo con exito!'
      });
      this.service.listar().subscribe(data => {
        this.service.periodosSubject.next(data);
        this.dialogRef.close();
      });
    });
  }

  cancelar() {
    this.dialogRef.close();
  }

}
