import { ReportsService } from './../../_controller/reports.service';
import { PagoDialogComponent } from './pago-dialog/pago-dialog.component';
import { ReciboService } from './../../_service/recibo.service';
import { IngresoService } from './../../_service/ingreso.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { Ingreso } from './../../_model/ingreso';
import { Recibo } from './../../_model/dto/recibo';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatSort, MatDialog } from '@angular/material';
import Swal from 'sweetalert2';
import * as moment from 'moment';
import { PropietarioDetail } from 'src/app/_model/dto/propietario-detail';

@Component({
  selector: 'app-pago',
  templateUrl: './pago.component.html',
  styleUrls: ['./pago.component.scss']
})
export class PagoComponent implements OnInit {

  dataSource: MatTableDataSource<Ingreso>;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  displayedColumns = ['creacion', 'total', 'estado', 'acciones'];

  constructor(public ingresoService: IngresoService,
    public reciboService: ReciboService,
    public dialog: MatDialog,
    public reportSerice: ReportsService) { }

  ngOnInit() {
    this.ingresoService.listarPageable(0, 10).subscribe(data => {
      data.content.forEach(element => {
        element.createdAt = moment(element.createdAt).format("DD/MM/YYYY HH:mm:ss");
      });
      this.ingresoService.ingresosSubject.next(data);
    });
    this.ingresoService.ingresosSubject.subscribe(data => {
      this.paginator.length = data.totalElements;
      this.dataSource = new MatTableDataSource(data.content);
      this.dataSource.sort = this.sort;
      this.paginator.pageIndex = data.pageable.number;
      this.paginator.pageSize = data.size;
    });
    this.paginator.firstPage();
  }

  eliminar(ingreso: Ingreso) {
    Swal.fire({
      title: 'Esta seguro que desea anular el pago?',
      showDenyButton: true,
      confirmButtonText: `Si`,
      denyButtonText: `No`,
      icon: "warning"
    }).then((result) => {
      if (result.isConfirmed) {
        this.reciboService.deleteById(ingreso.id).subscribe(p => {
          Swal.fire("Se anulo el pago con exito!", "", "success");
          // if (this.filter) {
          //   this.predioService.listarPageable(this.paginator.pageIndex, this.paginator.pageSize, this.filterBy, this.filter).subscribe(data => {
          //     this.predioService.prediosSubject.next(data);
          //   });
          // } else {
          this.ingresoService.listarPageable(this.paginator.pageIndex, this.paginator.pageSize).subscribe(data => {
            data.content.forEach(element => {
              element.createdAt = moment(element.createdAt).format("DD/MM/YYYY HH:mm:ss");
            });
            this.ingresoService.ingresosSubject.next(data);
          });
          // }
        });
      }
    })
  }

  mostrarMas(e: any) {
    // if (this.filter) {
    //   this.predioService.listarPageable(e.pageIndex, e.pageSize, this.filterBy, this.filter).subscribe(data => {
    //     this.predioService.prediosSubject.next(data);
    //   });
    // } else {
    this.ingresoService.listarPageable(e.pageIndex, e.pageSize).subscribe(data => {
      data.content.forEach(element => {
        element.createdAt = moment(element.createdAt).format("DD/MM/YYYY HH:mm:ss");
      });
      this.ingresoService.ingresosSubject.next(data);
    });
    // }
  }

  openDialog(ingreso?: Ingreso) {
    if (ingreso) {
      this.reciboService.getById(ingreso.id).subscribe(recibo => {
        this.dialog.open(PagoDialogComponent, {
          width: '800px',
          data: recibo
        });
      });
    } else {
      let recibo = new Recibo();
      recibo.ingreso = new Ingreso();
      recibo.propietarioDetail = new PropietarioDetail();
      recibo.ingreso.detalle = [];
      this.dialog.open(PagoDialogComponent, {
        width: '800px',
        height: '750px',
        data: recibo
      });
    }
  }

  print(ingreso?: Ingreso) {
    this.reciboService.getById(ingreso.id).subscribe(recibo => {
      this.reportSerice.openRecibo(recibo);
    });
  }

}
