import { ProcedimientoDialogComponent } from './procedimiento-dialog/procedimiento-dialog.component';
import { TasaService } from './../../_service/tasa.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { Tasa } from './../../_model/tasa';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatSort, MatDialog } from '@angular/material';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-procedimiento',
  templateUrl: './procedimiento.component.html',
  styleUrls: ['./procedimiento.component.scss']
})
export class ProcedimientoComponent implements OnInit {

  dataSource: MatTableDataSource<Tasa>;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  displayedColumns = ['procedimiento', 'precio', 'acciones'];

  constructor(public tasaService: TasaService, public dialog: MatDialog) { }

  ngOnInit() {
    this.tasaService.listarPageable(0, 10).subscribe(data => this.tasaService.tasasSubject.next(data));
    this.tasaService.tasasSubject.subscribe(data => {
      this.paginator.length = data.totalElements;
      this.dataSource = new MatTableDataSource(data.content);
      this.dataSource.sort = this.sort;
      this.paginator.pageIndex = data.pageable.number;
      this.paginator.pageSize = data.size;
      console.log(this.paginator);
    });
  }

  eliminar(tasa: Tasa) {
    Swal.fire({
      title: 'Esta seguro que desea eliminar el Procedimiento?',
      showDenyButton: true,
      confirmButtonText: `Si`,
      denyButtonText: `No`,
      icon: "warning"
    }).then((result) => {
      if (result.isConfirmed) {
        this.tasaService.deleteById(tasa.id).subscribe(p => {
          Swal.fire("Se elimino el Procedimiento con exito!", "", "success");
          // if (this.filter) {
          //   this.predioService.listarPageable(this.paginator.pageIndex, this.paginator.pageSize, this.filterBy, this.filter).subscribe(data => {
          //     this.predioService.prediosSubject.next(data);
          //   });
          // } else {
          this.tasaService.listarPageable(this.paginator.pageIndex, this.paginator.pageSize).subscribe(data => this.tasaService.tasasSubject.next(data));
          // }
        });
      }
    })
  }

  mostrarMas(e: any) {
    // if (this.filter) {
    //   this.predioService.listarPageable(e.pageIndex, e.pageSize, this.filterBy, this.filter).subscribe(data => {
    //     this.predioService.prediosSubject.next(data);
    //   });
    // } else {
    this.tasaService.listarPageable(e.pageIndex, e.pageSize).subscribe(data => {
      this.tasaService.tasasSubject.next(data);
    });
    // }
  }

  openDialog(tasa?: Tasa) {
    let tasa1 = tasa != null ? tasa : new Tasa();
    this.dialog.open(ProcedimientoDialogComponent, {
      width: '280px',
      data: tasa1
    });
  }

}
