import { PeriodoService } from './../../../_service/periodo.service';
import { ValorTerrenoRustico } from './../../../_model/valor-terreno-rustico';
import { Periodo } from './../../../_model/periodo';
import { Calidad } from './../../../_model/calidad';
import { Altitud } from './../../../_model/altitud';
import { GrupoTierra } from './../../../_model/grupo-tierra';
import { ValorTerrenoRusticoService } from './../../../_service/valor-terreno-rustico.service';
import { CalidadService } from './../../../_service/calidad.service';
import { AltitudService } from './../../../_service/altitud.service';
import { GrupoTierraService } from './../../../_service/grupo-tierra.service';
import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-rustico',
  templateUrl: './rustico.component.html',
  styleUrls: ['./rustico.component.scss']
})
export class RusticoComponent implements OnInit {

  tierras: GrupoTierra[];
  altitudes: Altitud[];
  calidades: Calidad[];
  periodos: Periodo[];
  valoresTerreno: ValorTerrenoRustico[];
  seleccionado: any;
  isActive: boolean;
  isActive1: boolean;
  isActive2: boolean;

  constructor(
    public periodoService: PeriodoService,
    public tierraService: GrupoTierraService,
    public altitudService: AltitudService,
    public calidadService: CalidadService,
    public valoresService: ValorTerrenoRusticoService
  ) { }

  ngOnInit() {
    this.periodoService.listar().subscribe(data => {
      console.log(data);

      this.periodos = data;
    });

    this.calidadService.listar().subscribe(data => {
      this.calidades = data;
      console.log(data);
    });

    this.tierraService.listar().subscribe(data => {
      this.tierras = data;
      console.log(data);
    });

    this.altitudService.listar().subscribe(data => {
      this.altitudes = data;
      console.log(data);
    });
  }

  elegir(periodo_id: number) {
    let flag: boolean = false;
    this.valoresService.listar(periodo_id).subscribe(data => {
      if (data.length == 0) {
        this.valoresTerreno = [];
        let valorTerrenoRustico: ValorTerrenoRustico;
        this.tierras.forEach(tierra => {
          this.altitudes.forEach(altitud => {
            this.calidades.forEach(calidad => {
              if (tierra.id == 3 && altitud.id == 1) {
                return;
              }
              if (flag) {
                return;
              }
              if (tierra.id == 4) {
                valorTerrenoRustico = new ValorTerrenoRustico();
                valorTerrenoRustico.periodo = new Periodo();
                valorTerrenoRustico.periodo.id = periodo_id;
                valorTerrenoRustico.grupoTierra = tierra;
                valorTerrenoRustico.unidadMedida = "HEC";
                valorTerrenoRustico.valor = 0;
                this.valoresTerreno.push(valorTerrenoRustico);
                flag = true;
              } else {
                valorTerrenoRustico = new ValorTerrenoRustico();
                valorTerrenoRustico.periodo = new Periodo();
                valorTerrenoRustico.periodo.id = periodo_id;
                valorTerrenoRustico.grupoTierra = tierra;
                valorTerrenoRustico.altitud = altitud;
                valorTerrenoRustico.calidad = calidad;
                valorTerrenoRustico.unidadMedida = "HEC";
                valorTerrenoRustico.valor = 0;
                this.valoresTerreno.push(valorTerrenoRustico);
              }
            });
          });
        });
      } else {
        this.valoresTerreno = data;
      }
    });
  }

  guardar() {
    this.valoresService.grabar(this.valoresTerreno).subscribe(() => {
      Swal.fire("Muy bien", "Se registraron los valores con exito!", "success");
    });
  }

}
