import { AuthService } from './auth.service';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { Recibo } from './../_model/dto/recibo';
import { Injectable } from '@angular/core';
import { environment } from './../../environments/environment';
import { ExceptionService } from './exception.service';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ReciboService {

  url: string = `${environment.HOST}/recibos`;

  constructor(public http: HttpClient, public exception: ExceptionService, public authService: AuthService) { }

  crear(recibo: Recibo) {
    return this.http.post<Recibo>(this.url, recibo, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  getById(id: number) {
    return this.http.get<Recibo>(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  deleteById(id: number) {
    return this.http.delete(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
