import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { Usuario } from './../../../_model/usuario';
import { UsuarioService } from './../../../_service/usuario.service';
import { PersonaService } from './../../../_service/persona.service';
import { Component, OnInit, Inject } from '@angular/core';
import { Propietario } from 'src/app/_model/propietario';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-usuario-dialog',
  templateUrl: './usuario-dialog.component.html',
  styleUrls: ['./usuario-dialog.component.scss']
})
export class UsuarioDialogComponent implements OnInit {

  roles = [
    { id: 1, nombre: "ROLE_CAJERO", rol: "CAJERO" },
    { id: 2, nombre: "ROLE_RENTAS", rol: "RENTAS" },
    { id: 3, nombre: "ROLE_TESORERO", rol: "TESORERO" },
    { id: 4, nombre: "ROLE_ADMIN", rol: "ADMINISTRADOR DE USUARIOS" }
  ];

  constructor(public dialogRef: MatDialogRef<UsuarioDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public usuario: Usuario,
    public personaService: PersonaService, public usuarioService: UsuarioService) { }

  ngOnInit() {
    this.usuario.persona.tipoDocumento = "DNI";
    this.usuario.persona.propietario = new Propietario();
    this.usuario.persona.propietario.tipo = "PERSONA";
    this.usuario.fechaCreacion = undefined;
    this.usuario.fechaModificacion = undefined;
    console.log(this.usuario);
    if (this.usuario.id) {
      this.usuario.roles = this.roles.filter(rol => this.usuario.roles.map(r => r.id).includes(rol.id));
    }
  }

  findConstribuyente(tipo: string, documento: string) {
    this.personaService.findByTipoAndDocumento(tipo, documento).subscribe(persona => {
      if (persona.id) {
        this.usuario.persona = persona;
      }
    });
  }

  guardar() {
    if (this.usuario.id) {
      this.usuarioService.modificarUsuario(this.usuario).subscribe(() => {
        Swal.fire("Modificacion con exito", "", "success");
        this.dialogRef.close();
        this.usuarioService.listar().subscribe(usuarios => this.usuarioService.usuarioSubject.next(usuarios));
      });
    } else {
      this.usuarioService.grabarUsuario(this.usuario).subscribe(() => {
        Swal.fire("Registro con exito", "", "success");
        this.dialogRef.close();
        this.usuarioService.listar().subscribe(usuarios => this.usuarioService.usuarioSubject.next(usuarios));
      });
    }
  }

}
