import { ExceptionService } from './exception.service';
import { catchError } from 'rxjs/operators';
import { AuthService } from './auth.service';
import { Impuesto } from './../_model/impuesto';
import { Subject, throwError } from 'rxjs';
import { ImpuestoDetail } from './../_model/dto/impuesto-detail';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ImpuestoDetailService {

  url: string = `${environment.HOST}/impuestos`;

  impuestosSubject = new Subject<any[]>();

  constructor(public http: HttpClient, public authService: AuthService, public exception: ExceptionService) { }

  listarPorId(id: number) {
    return this.http.get<ImpuestoDetail>(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  getByPredioId(id: number) {
    return this.http.get<Impuesto>(`${this.url}/predio/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  listarPorPropietarioId(id: number) {
    return this.http.get<any[]>(`${this.url}/propietarios/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
