import { MatBottomSheetRef } from '@angular/material';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-bottom-sheet-mensaje',
  templateUrl: './bottom-sheet-mensaje.component.html',
  styleUrls: ['./bottom-sheet-mensaje.component.scss']
})
export class BottomSheetMensajeComponent implements OnInit {

  mensajeMantenimiento: boolean = false;
  constructor(public mensaje: MatBottomSheetRef<BottomSheetMensajeComponent>) { }

  ngOnInit() {
  }

  close(): void {
    document.cookie = `mensajeMantenimiento=${this.mensajeMantenimiento}; expires = Tue, 01 Jan 2030 12:00:00 UTC`;
    this.mensaje.dismiss();
    // event.preventDefault();
  }
}
