  openReporteCaja(from: string, to: string, ingresos: Ingreso[]): void {
    const pdf = new PdfMakeWrapper();
    const data = [];
    const tableLayout = {
      hLineWidth: (i, node) => {
        if (i === 0 || i === node.table.body.length) {
          return 0;
        }
        return (i === 1) ? 2 : 1;
      },
      vLineWidth: i => 1,
      vLineColor: () => "#fff",
      hLineColor: () => "#fff",
      paddingLeft: i => i === 0 ? 2 : 4,
      paddingRight: (i, node) => (i === node.table.widths.length - 1) ? 0 : 4,
      fillColor: i => {
        if (i === 0) {
          return "#ed7d31";
        }
        return i % 2 == 0 ? "#f8cbad" : "#fce4d6";
      }
    };
    pdf.pageMargins([40, 20, 40, 50]);
    pdf.watermark(new Txt("SISMAT").opacity(0.2).end);
    let headers = ["Recibo N", "Fecha y Hora de Emisin", "Concepto", "Importe", "Monto Afecto"];
    data.push(headers.map(header => new Txt(header).color("#fff").fontSize(9).bold().end));
    ingresos.forEach(ingreso => data.push([
      this.padLeft(ingreso.id.toString(), 11),
      moment(ingreso.createdAt).format("DD/MM/YYYY HH:mm:ss"),
      ingreso.detalle.map(detalle => detalle.concepto).toString(),
      `S/.${ingreso.total.toFixed(2)}`,
      `S/.${ingreso.montoAfecto.toFixed(2)}`
    ].map(el => new Txt(el).color("#000").fontSize(9).end)));
    const table = new Table(data)
      .widths([70, 100, 178, 68, 68])
      .layout(tableLayout)
      .color("#555")
      .end;
    pdf.footer((currentPage, pageCount) => {
      return [
        { text: `pgina ${currentPage.toString()} de ${pageCount}`, alignment: 'right', margin: [40, 0] }
      ];
    });
    //Encavezado
    pdf.add(new Txt(`MUNICIPALIDAD PROVINCIAL DE AIJA`)
      .bold()
      .fontSize(15)
      .color("#000")
      .alignment("center")
      .end);
    pdf.add(pdf.ln());
    pdf.add(new Txt("REPORTE DE CAJA")
      .fontSize(16)
      .color("#000")
      .alignment("center")
      .end);
    pdf.add(pdf.ln());
    pdf.add(new Txt(`${moment(from).format("DD/MM/YYYY")} - ${moment(to).format("DD/MM/YYYY")}`)
      .fontSize(14)
      .color("#000")
      .alignment("center")
      .end);
    pdf.add(pdf.ln());
    pdf.add(table);
    pdf.add(new Txt(`TOTAL:       S/.${ingresos.map(ingreso => ingreso.montoAfecto).reduce((sum, element) => sum + element, 0).toFixed(2)}`)
      .bold()
      .fontSize(10)
      .color("#000")
      .alignment("right")
      .end);
    new Img("assets/logo.png")
      .fit([80, 80])
      .alignment("left")
      .opacity(0.8)
      .absolutePosition(30, 10)
      .build()
      .then(img => {
        pdf.header(img);
        pdf.create().open();
      });
  }

  openReporteCPU(autoavaluoCpu: AutoavaluoCpu) {

    let documentCPU = {
      abbreviation: "PCPU",
      type: "PREDIO CENTRO\nPOBLADO URBANO",
      period: autoavaluoCpu.terrenoCPU.predio.periodo.anio,
      contribuyentes: autoavaluoCpu.propietariosDetail,
      predio: {
        condicion: autoavaluoCpu.terrenoCPU.predio.condicion.toUpperCase(),
        estado: autoavaluoCpu.terrenoCPU.predio.estado.toUpperCase(),
        tipo: autoavaluoCpu.terrenoCPU.predio.tipo.toUpperCase(),
        uso: autoavaluoCpu.terrenoCPU.predio.uso.toUpperCase(),
        ubicacion: `${autoavaluoCpu.terrenoCPU.predio.ubicacion.descripcion.toUpperCase()} ${autoavaluoCpu.terrenoCPU.predio.ubicacion.numero}`
      },
      construccion: {
        data: autoavaluoCpu.terrenoCPU.predio.construcciones,
        total: `S /.${autoavaluoCpu.terrenoCPU.predio.totalConstruccion.toFixed(2)}`
      },
      instalacion: {
        data: autoavaluoCpu.terrenoCPU.predio.instalacionesPredio,
        total: `S /.${autoavaluoCpu.terrenoCPU.predio.totalInstalaciones}`
      },
      terreno: {
        calzada: autoavaluoCpu.terrenoCPU.calzada.descripcion.toUpperCase(),
        ancho: autoavaluoCpu.terrenoCPU.medida.intervalo.toUpperCase(),
        area: autoavaluoCpu.terrenoCPU.area,
        arancel: `S /.${autoavaluoCpu.terrenoCPU.arancel.toFixed(2)}`,
        servicios: autoavaluoCpu.terrenoCPU.servicioBasico.descripcion.toUpperCase(),
        total: `S /.${autoavaluoCpu.terrenoCPU.valorTotal.toFixed(2)}`
      },
      total: `S /.${autoavaluoCpu.terrenoCPU.predio.autoavaluo.toFixed(2)}`
    }

    this.generarReporte(documentCPU);
  }

