import { AbstractControl, ValidationErrors, ValidatorFn } from "@angular/forms";

export function validatorViaName(vias: string[]): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    return vias.filter(via => via == control.value).length == 0 ? { validatorVia: { value: "Digite una via valida" } } : null;
  };
}

