import { AuthService } from './auth.service';
import { Tasa } from './../_model/tasa';
import { catchError } from 'rxjs/operators';
import { throwError, Subject } from 'rxjs';
import { Injectable } from '@angular/core';
import { environment } from './../../environments/environment';
import { ExceptionService } from './exception.service';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class TasaService {

  url: string = `${environment.HOST}/tasas`;
  tasasSubject = new Subject<any>();

  constructor(public http: HttpClient, public exception: ExceptionService, public authService: AuthService) { }

  crear(tasa: Tasa) {
    return this.http.post<Tasa>(this.url, tasa, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  modificar(tasa: Tasa) {
    return this.http.put<Tasa>(this.url, tasa, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  getById(id: number) {
    return this.http.get<Tasa>(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  listar() {
    return this.http.get<Tasa[]>(this.url, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  deleteById(id: number) {
    return this.http.delete(`${this.url}/${id}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  listarPageable(page: number, size: number) {
    return this.http.get<any>(`${this.url}/pageable?page=${page}&size=${size}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
