import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { PredioService } from './../../../_service/predio.service';
import { ImpuestoDetailService } from './../../../_service/impuesto-detail.service';
import { GeocodeService } from './../../../_service/geocode.service';
import { Via } from './../../../_model/via';
import { ViaService } from './../../../_service/via.service';
import { AutoavaluoUrbanoService } from './../../../_service/autoavaluo-urbano.service';
import { TerrenoUrbano } from './../../../_model/terreno-urbano';
import { AutoavaluoUrbano } from './../../../_model/dto/autoavaluo-urbano';
import { Component, OnInit, Inject } from '@angular/core';
import { ENTER, COMMA } from '@angular/cdk/keycodes';
import { Persona } from './../../../_model/persona';
import { Empresa } from './../../../_model/empresa';
import { Contribuyente } from './../../../_model/dto/contribuyente';
import { Ubicacion } from './../../../_model/ubicacion';
import { ReportsService } from './../../../_controller/reports.service';
import { EmpresaService } from './../../../_service/empresa.service';
import { PersonaService } from './../../../_service/persona.service';
import { PropietarioDetail } from './../../../_model/dto/propietario-detail';
import { Predio } from './../../../_model/predio';
import { Periodo } from './../../../_model/periodo';
import Swal from 'sweetalert2';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { validatorViaName } from 'src/app/_directives/validator-via.directive';
@Component({
  selector: 'app-predio-urbano',
  templateUrl: './predio-urbano.component.html',
  styleUrls: ['./predio-urbano.component.scss']
})
export class PredioUrbanoComponent implements OnInit {

  propietariosDetail: PropietarioDetail[];
  terrenoUrbano: TerrenoUrbano;
  predio: Predio;
  periodo: Periodo;
  nombreVias: string[];
  vias: Via[];
  viasCtrl = new FormControl();
  filteredVias: Observable<string[]>;
  viasNew: Via[];
  enabled = true;
  tipoSelected: any;
  selectable: any;

  constructor(public personaService: PersonaService,
    public empresaService: EmpresaService,
    public predioService: PredioService,
    public dialog: MatDialogRef<PredioUrbanoComponent>,
    @Inject(MAT_DIALOG_DATA) public autoavaluoUrbano: AutoavaluoUrbano,
    public autoavaluoUrbanoService: AutoavaluoUrbanoService,
    public reportService: ReportsService,
    public viaService: ViaService,
    public geocodeService: GeocodeService,
    public impuestoDetailService: ImpuestoDetailService) { }


  ngOnInit() {
    this.viaService.listarVias().subscribe(data => {
      this.nombreVias = data;
      this.viasCtrl.validator = validatorViaName(this.nombreVias);
      this.filteredVias = this.viasCtrl.valueChanges
        .pipe(
          startWith(''),
          map(via => via ? this._filterVias(via) : this.nombreVias.slice())
        );
    });
    this.viaService.listar().subscribe(data => {
      this.vias = data;
      if (this.autoavaluoUrbano.terrenoUrbano.via.id) {
        this.updateVias();
        this.terrenoUrbano.via = this.viasNew.filter(via => via.id == this.terrenoUrbano.via.id)[0];
      }
    });
    //Marcar en el mapa
    if (this.autoavaluoUrbano.terrenoUrbano.predio.ubicacion) {
      this.markerPosition = {
        lat: this.autoavaluoUrbano.terrenoUrbano.predio.ubicacion.latitud,
        lng: this.autoavaluoUrbano.terrenoUrbano.predio.ubicacion.longitud
      }
    }
    //Fin marcar en el mapa
    this.propietariosDetail = this.autoavaluoUrbano.propietariosDetail;
    this.predio = this.autoavaluoUrbano.terrenoUrbano.predio;
    this.terrenoUrbano = this.autoavaluoUrbano.terrenoUrbano;
    this.predio.ubicacion = this.autoavaluoUrbano.terrenoUrbano.predio.ubicacion;
    this.periodo = this.predio.periodo;
    if (this.predio.construcciones.length > 0) {
      this.predio.construcciones.forEach(construccion => {
        construccion.murcol = construccion.categorias.charAt(0);
        construccion.techo = construccion.categorias.charAt(1);
        construccion.piso = construccion.categorias.charAt(2);
        construccion.puertventa = construccion.categorias.charAt(3);
        construccion.revestimiento = construccion.categorias.charAt(4);
        construccion.banio = construccion.categorias.charAt(5);
        construccion.instelecsanit = construccion.categorias.charAt(6);
      });
    }
  }

  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  personas: Persona[] = [];
  empresas: Empresa[] = [];
  tipoSelectedRepresentante: any;
  // ejemplos
  contribuyente = new Contribuyente();
  tipoDocumentos: any[] = [
    { documento: "DNI", length: 8, ejemplo: "75896541", meta: { nombre: "Apellidos y Nombres", placeholder: "RODRIGUEZ ARTEAGA FRANCISCO ROBERTO" } },
    { documento: "RUC", length: 11, ejemplo: "20542596352", meta: { nombre: "Razon Social", placeholder: "GRUPO ARCOS SRL" } },
    { documento: "CE", length: 20, ejemplo: "001043328", meta: { nombre: "Nombres", placeholder: "GIANLUCA LAPADULA" } }
  ];
  tipoDocumentosRepresentante: any[] = [
    { documento: "DNI", length: 8, ejemplo: "75896541", meta: { nombre: "Apellidos y Nombres", placeholder: "RODRIGUEZ ARTEAGA FRANCISCO ROBERTO" } },
    { documento: "CE", length: 20, ejemplo: "001043328", meta: { nombre: "Nombres", placeholder: "GIANLUCA LAPADULA" } }
  ];

  public _filterVias(value: string): string[] {
    const filterValue = value.toLowerCase();
    return this.nombreVias.filter(via => via.toLowerCase().indexOf(filterValue) === 0);
  }

  recibirPeriodo(periodo: Periodo) {
    this.periodo = periodo;
  }

  removePropietario(contribuyente: PropietarioDetail): void {
    const index = this.propietariosDetail.indexOf(contribuyente);
    if (index >= 0) {
      this.propietariosDetail.splice(index, 1);
    }
  }

  agregar(tipoDocumento: any, tipoDocumentoRepresentante: any): void {
    this.contribuyente.tipoDocumento = tipoDocumento.documento;
    let propietarioDetail = new PropietarioDetail();
    if (this.contribuyente.tipoDocumento == "DNI" || this.contribuyente.tipoDocumento == "CE") {
      let persona = new Persona();
      persona.id = this.contribuyente.id;
      persona.tipoDocumento = this.contribuyente.tipoDocumento;
      persona.documento = this.contribuyente.documento;
      persona.nombres = this.contribuyente.nombre;
      propietarioDetail.persona = persona;
      this.propietariosDetail.push(propietarioDetail);
    } else if (this.contribuyente.tipoDocumento == "RUC") {
      let empresa = new Empresa();
      empresa.id = this.contribuyente.id;
      empresa.razonSocial = this.contribuyente.nombre;
      empresa.ruc = this.contribuyente.documento;
      empresa.representante = this.contribuyente.representante;
      empresa.representante.tipoDocumento = tipoDocumentoRepresentante.documento;
      propietarioDetail.empresa = empresa;
      this.propietariosDetail.push(propietarioDetail);
    }
    this.contribuyente = new Contribuyente();
    this.tipoSelectedRepresentante = undefined;
  }

  findConstribuyente(tipo: string, documento: string) {
    if (tipo == "RUC") {
      this.empresaService.findByRuc(documento).subscribe(empresa => {
        if (empresa.id != null) {
          this.contribuyente.id = empresa.id;
          this.contribuyente.nombre = empresa.razonSocial;
          this.contribuyente.documento = empresa.ruc;
          this.contribuyente.representante = empresa.representante;
          if (empresa.representante.tipoDocumento === "DNI") {
            this.tipoSelectedRepresentante = { documento: "DNI", length: 8, ejemplo: "75896541", meta: { nombre: "Apellidos y Nombres", placeholder: "RODRIGUEZ ARTEAGA FRANCISCO ROBERTO" } };
          } else if (empresa.representante.tipoDocumento === "CE") {
            this.tipoSelectedRepresentante = { documento: "CE", length: 20, ejemplo: "001043328", meta: { nombre: "Nombres", placeholder: "GIANLUCA LAPADULA" } };
          }
        }
      });
    } else {
      this.personaService.findByTipoAndDocumento(tipo, documento).subscribe(persona => {
        if (persona.id != null) {
          this.contribuyente.id = persona.id;
          this.contribuyente.tipoDocumento = persona.tipoDocumento;
          this.contribuyente.documento = persona.documento;
          this.contribuyente.nombre = persona.nombres;
        }
      });
    }
  }

  findRepresentante(tipo: string, documento: string) {
    this.personaService.findByTipoAndDocumento(tipo, documento).subscribe(persona => {
      if (persona.id != null) {
        this.contribuyente.representante.id = persona.id;
        this.contribuyente.representante.tipoDocumento = persona.tipoDocumento;
        this.contribuyente.representante.documento = persona.documento;
        this.contribuyente.representante.nombres = persona.nombres;
      }
    });
  }

  compare(option1: any, option2: any) {
    return option1.documento === option2.documento;
  }


  save() {
    this.predio.construcciones.forEach(construccion => {
      construccion.categorias = `${construccion.murcol}${construccion.techo}${construccion.piso}${construccion.puertventa}${construccion.revestimiento}${construccion.banio}${construccion.instelecsanit}`;
    });
    this.predio.periodo = this.periodo;
    this.predio.ubicacion.descripcion = this.terrenoUrbano.via.nombreVia;
    this.terrenoUrbano.predio = this.predio;
    this.autoavaluoUrbano.propietariosDetail = this.propietariosDetail;
    this.autoavaluoUrbano.terrenoUrbano = this.terrenoUrbano;
    this.autoavaluoUrbanoService.crear(this.autoavaluoUrbano).subscribe(data => {
      Swal.fire({
        title: 'Se registro el autoavaluo',
        showDenyButton: true,
        confirmButtonText: `Mostrar Hoja de Resumen`,
        denyButtonText: `Registrar otro`,
        icon: "success"
      }).then((result) => {
        if (result.isConfirmed) {
          this.impuestoDetailService.listarPorId(data.impuestoId).subscribe(impuesto => {
            this.reportService.openReporteLiquidacion(impuesto, 'A4');
          });
        } else if (result.isDenied) {
          this.initialize();
        }
        this.reportService.openReporteUrbano(data);
        this.predioService.listarPageable(0, 10).subscribe(data => this.predioService.prediosSubject.next(data));
        this.dialog.close();
      })
    });
    this.predioService.listarPageable(0, 10).subscribe(data => this.predioService.prediosSubject.next(data));
  }

  initialize() {
    this.terrenoUrbano = new TerrenoUrbano();
    this.terrenoUrbano.via = new Via();
    this.autoavaluoUrbano = new AutoavaluoUrbano();
    this.propietariosDetail = [];
    this.predio = new Predio();
    this.predio.construcciones = [];
    this.predio.ubicacion = new Ubicacion();
    this.personas = [];
    this.empresas = [];
  }

  updateVias() {
    this.viasNew = this.vias.filter(via => via.nombreVia == this.terrenoUrbano.via.nombreVia);
  }


  title = "titulo";
  position = {
    lat: -9.7811,
    lng: -77.6126
  };

  label = {
    color: "red",
    // text: "Marcador"
  };

  markerOptions: google.maps.MarkerOptions = { draggable: true };
  markerPosition: google.maps.LatLngLiteral;
  geocode: google.maps.Geocoder;

  setMarker(event: google.maps.MapMouseEvent) {
    this.markerPosition = event.latLng.toJSON();
    this.predio.ubicacion.latitud = this.markerPosition.lat;
    this.predio.ubicacion.longitud = this.markerPosition.lng;
    this.geocodeService.getAddress(this.markerPosition.lat, this.markerPosition.lng).subscribe(data => console.log(data));
  }

}
