import { ExceptionService } from './exception.service';
import { catchError } from 'rxjs/operators';
import { AuthService } from './auth.service';
import { ValorTerrenoUrbano } from './../_model/valor-terreno-urbano';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ValorTerrenoUrbanoService {

  url: string = `${environment.HOST}/valores-urbanos`;

  constructor(public http: HttpClient, public authService: AuthService, public exception: ExceptionService) { }

  listar(periodoId: number) {
    return this.http.get<ValorTerrenoUrbano[]>(`${this.url}/periodo/${periodoId}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  grabar(valoresTerrenoUrbano: ValorTerrenoUrbano[]) {
    return this.http.post(this.url, valoresTerrenoUrbano, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }
}
