import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})
export class ExceptionService {

  constructor(public router: Router) { }

  catchValidation(e: any) {
    if (e.status == 400) {
      let errores: string = "<ul>";
      e.error.forEach(element => {
        errores += `<li>${element.detalles}</li>`
      });
      errores += '</ul>';
      Swal.fire({
        title: 'Ocurrio un error',
        html: errores,
        icon: 'error'
      });
    }

    if (e.status == 500) {
      Swal.fire("Ocurrio un error", "Error en el sistema, por favor comuniquese con informatica", "error");
    }

    if (e.status == 401) {
      this.router.navigate(['/login']);
    }

    if (e.status == 403) {
      Swal.fire("Acceso denegado", "Usted no tiene acceso a este recurso!", "error");
    }
  }
}
