import { ExceptionService } from './exception.service';
import { Router } from '@angular/router';
import { AuthService } from './auth.service';
import { HttpClient } from '@angular/common/http';
import { environment } from './../../environments/environment';
import { Usuario } from './../_model/usuario';
import { catchError } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import Swal from 'sweetalert2';
import { throwError, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UsuarioService {

  url: string = `${environment.HOST}/usuarios`;
  usuarioSubject = new Subject<Usuario[]>();

  constructor(
    public http: HttpClient,
    public authService: AuthService,
    public router: Router,
    public exception: ExceptionService
  ) { }

  listar() {
    return this.http
      .get<Usuario[]>(this.url, { headers: this.authService.headers })
      .pipe(
        catchError((e) => {
          this.exception.catchValidation(e);
          return throwError(e);
        })
      );
  }

  modificar(usuario: Usuario) {
    return this.http
      .put(`${this.url}/perfil`, usuario, { headers: this.authService.headers })
      .pipe(
        catchError((e) => {
          this.exception.catchValidation(e);
          return throwError(e);
        })
      );
  }

  modificarUsuario(usuario: Usuario) {
    return this.http
      .put(this.url, usuario, { headers: this.authService.headers })
      .pipe(
        catchError((e) => {
          this.exception.catchValidation(e);
          return throwError(e);
        })
      );
  }

  grabarUsuario(usuario: Usuario) {
    return this.http
      .post(this.url, usuario, { headers: this.authService.headers })
      .pipe(
        catchError((e) => {
          this.exception.catchValidation(e);
          return throwError(e);
        })
      );
  }

  listarPorUsername(usuario: string) {
    return this.http.get<Usuario>(`${this.url}/${usuario}`, {
      headers: this.authService.headers
    })
      .pipe(
        catchError((e) => {
          this.exception.catchValidation(e);
          return throwError(e);
        })
      );
  }
}
