import { AuthService } from './auth.service';
import { ExceptionService } from './exception.service';
import { catchError } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { environment } from './../../environments/environment';
import { Injectable } from '@angular/core';
import { throwError, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PredioService {

  prediosSubject = new Subject<any>();

  url: string = `${environment.HOST}/predios`;

  constructor(public http: HttpClient, public exception: ExceptionService, public authService: AuthService) { }

  listarPageable(page: number, size: number, filterBy?: string, filter?: string) {
    let route = `/pageable?page=${page}&size=${size}`
    if (filterBy) {
      route += `&filterBy=${filterBy}&filter=${filter}`;
    }
    return this.http.get<any>(`${this.url}${route}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

  deleteById(predioId: number) {
    return this.http.delete(`${this.url}/${predioId}`, { headers: this.authService.headers }).pipe(
      catchError(e => {
        this.exception.catchValidation(e);
        return throwError(e);
      })
    );
  }

}
