import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from './../../environments/environment';
import { Usuario } from './../_model/usuario';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  public _usuario: Usuario;
  public _token: string;
  url: string = `${environment.HOST}/oauth/token`;
  constructor(public http: HttpClient) { }

  login(usuario: Usuario): Observable<any> {
    const credenciales = btoa(`sismatFront:123456`);
    const httpHeaders = new HttpHeaders({
      'Content-Type': 'application/x-www-form-urlencoded',
      'Authorization': 'Basic ' + credenciales
    });
    let params = new URLSearchParams();
    params.set('grant_type', 'password');
    params.set('username', usuario.nombre);
    params.set('password', usuario.contrasenia);
    return this.http.post(this.url, params.toString(), { headers: httpHeaders })
  }

  guardarToken(access_token: string) {
    this._token = access_token;
    sessionStorage.setItem('token', access_token);
  }

  guardarUsuario(access_token: string) {
    let payload = this.obtenerDatosToken(access_token);
    this._usuario = new Usuario();
    this._usuario.nombre = payload.user_name;
    sessionStorage.setItem('usuario', JSON.stringify(this._usuario));
  }

  public get usuario(): Usuario {
    if (this._usuario != null) {
      return this._usuario;
    } else if (sessionStorage.getItem('usuario') != null) {
      this._usuario = JSON.parse(sessionStorage.getItem('usuario')) as Usuario;
      return this._usuario;
    }
    return new Usuario();
  }

  public get token(): string {
    if (this._token != null) {
      return this._token;
    } else if (sessionStorage.getItem('token') != null) {
      this._token = sessionStorage.getItem('token');
      return this._token;
    }
    return null;
  }

  obtenerDatosToken(access_token: string): any {
    if (access_token != null) {
      return JSON.parse(atob(access_token.split(".")[1]));
    }
    return null;
  }

  isAuthenticated(): boolean {
    let payload = this.obtenerDatosToken(this.token);
    if (payload != null && payload.user_name && payload.user_name.length > 0) {
      return true;
    }
    return false;
  }

  logout() {
    this._token = null;
    this._usuario = null;
    sessionStorage.clear();
    sessionStorage.removeItem('token');
    sessionStorage.removeItem('usuario');
    window.location.reload();
  }

  public get headers(): HttpHeaders {
    let httpHeaders: HttpHeaders = new HttpHeaders();
    let token = this.token;
    if (token != null) {
      return httpHeaders.append('Authorization', `Bearer ${this.token}`);
    }
    return httpHeaders;
  }
}
