import { ValorTerrenoCP } from './../../../_model/valorTerrenoCP';
import { ValorTerrenoCpService } from './../../../_service/valor-terreno-cp.service';
import { ServicioBasico } from './../../../_model/servicioBasico';
import { Calzada } from './../../../_model/calzada';
import { Medida } from './../../../_model/medida';
import { CalzadaService } from './../../../_service/calzada.service';
import { MedidaService } from './../../../_service/medida.service';
import { ServicioBasicoService } from './../../../_service/servicio-basico.service';
import { Periodo } from './../../../_model/periodo';
import { PeriodoService } from './../../../_service/periodo.service';
import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-cpu',
  templateUrl: './cpu.component.html',
  styleUrls: ['./cpu.component.scss']
})
export class CpuComponent implements OnInit {

  medidas: Medida[];
  calzadas: Calzada[];
  servicios: ServicioBasico[];
  periodos: Periodo[];
  valoresTerreno: ValorTerrenoCP[];
  seleccionado: any;
  isActive: boolean;
  isActive1: boolean;
  isActive2: boolean;

  constructor(public periodoService: PeriodoService,
    public servicioService: ServicioBasicoService,
    public medidaService: MedidaService,
    public calzadaService: CalzadaService,
    public valorTerrenoCpService: ValorTerrenoCpService) { }

  ngOnInit() {
    this.periodoService.listar().subscribe(data => {
      this.periodos = data;
    });

    this.servicioService.listar().subscribe(data => {
      this.servicios = data;
    });

    this.medidaService.listar().subscribe(data => {
      this.medidas = data;
    });

    this.calzadaService.listar().subscribe(data => {
      this.calzadas = data;
    });
  }

  elegir(periodo_id : number) {
    this.valorTerrenoCpService.listar(periodo_id).subscribe(data => {
      if (data.length == 0) {
        console.log("No hay datos");
        this.valoresTerreno = [];
        let valorTerrenoCP : ValorTerrenoCP ;
        this.calzadas.forEach(calzada => {
          this.medidas.forEach(medida => {
            this.servicios.forEach(servicio => {  
              valorTerrenoCP = new ValorTerrenoCP();
              valorTerrenoCP.calzada = calzada;
              valorTerrenoCP.medida = medida;
              valorTerrenoCP.servicioBasico = servicio;
              valorTerrenoCP.periodo = new Periodo();
              valorTerrenoCP.periodo.id = periodo_id;
              valorTerrenoCP.valor = 0;
              this.valoresTerreno.push(valorTerrenoCP);
            })
          });
        });
      } else {
        this.valoresTerreno = data;
      }
    });
  }

  guardar() {
    this.valorTerrenoCpService.grabar(this.valoresTerreno).subscribe(() => {
      Swal.fire("Muy bien", "Se registraron los valores con exito!","success");
    });
  }

}
