import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CookiesService {

  constructor() { }

  getCookie(name : string){
    let cook = 'false';
    let c = document.cookie.split(';');
    c.forEach( element => {
      if(element.split('=')[0] == name){
        cook = element.split('=')[1];
      }
    });
    return cook;
  }
}
